




































































































































































































import {
  Component,
  Prop,
  Vue,
  Model,
  Watch,
  Inject,
} from "vue-property-decorator";
// @ts-ignore
import PageComponent from "@/components/PageComponent.vue";
import GithubConfig from "@/util/GithubConfig";
@Component({
  components: {
    PageComponent,
  },
})
export default class BlogList extends Vue {
  blogContent: any = {
    labels: [],
    created_at: "",
    body: "",
    milestone: {},
    comments: 0,
  };
  markDownBody: string = "";
  commentList: any = [];
  pageNum: number = 1;
  pageSize: number = 10;
  commentBody: string = "";
  previewMode: boolean = false;
  previewContent: string = "";
  created() {
    if (
      window.location.href.indexOf("?code=") !== -1 &&
      !this.$route.query.code
    ) {
      (async () => {
        await this.$githubApi.getToken({
          code: window.location.href.substring(
            window.location.href.indexOf("?code=") + 6,
            window.location.href.indexOf("#")
          ),
        });
        let loginUserInfo: any = await this.$githubApi.getLoginUserInfo();
        localStorage.setItem(
          GithubConfig.LOCALSTORAGE_LOGIN_AVATAR,
          loginUserInfo.avatar_url
        );
        window.location.href =
          window.location.href.substring(
            0,
            window.location.href.indexOf("?code=")
          ) + window.location.href.substring(window.location.href.indexOf("#"));
      })();
    } else {
      this.initData();
    }
  }
  toLogin() {
    // console.log(window.location.href)
    this.$githubApi.toLogin(window.location.href);
  }
  loginOut() {
    localStorage.setItem(GithubConfig.LOCALSTORAGE_NAME, "");
    localStorage.setItem(GithubConfig.LOCALSTORAGE_LOGIN_AVATAR, "");
    window.history.go(0);
  }
  get loginInfo() {
    return {
      accessToken: localStorage.getItem(GithubConfig.LOCALSTORAGE_NAME),
      loginAvatar: localStorage.getItem(GithubConfig.LOCALSTORAGE_LOGIN_AVATAR),
    };
  }
  async initData(
    { toLastPage }: { toLastPage: boolean } = { toLastPage: false }
  ) {
    (async () => {
      if (
        this.$store.state.templateIssuesInfo.id &&
        this.$store.state.templateIssuesInfo.id ===
          this.$route.query.issueNumber
      ) {
        this.blogContent = this.$store.state.templateIssuesInfo;
      } else {
        this.blogContent = await this.$githubApi.getIssuesContent({
          issueNumber: this.$route.query.issueNumber,
        });
      }
      this.markDownBody = <string>(
        await this.$githubApi.getMdContent({ text: this.blogContent.body })
      );
      this.markDownBody = this.markDownBody.replace(
        /<a href="([^"]*).* src="([^"]*).* alt="([^"]*)".* data-canonical-src="([^"]*)".*<\/a>/gi,
        `<a href="$4" target="_blank" rel="nofollow"><img src="$4" alt="$3" data-canonical-src="$4" style="max-width:100%;"></a>`
      );
      if (toLastPage) {
        this.pageNum = Math.ceil(this.blogContent.comments / this.pageSize);
        this.getCommentList();
      }
    })();
    if (!toLastPage) {
      this.getCommentList();
    }
  }
  async getCommentList() {
    this.commentList = await this.$githubApi.getCommentList4Issues({
      issueNumber: this.$route.query.issueNumber,
      page: this.pageNum,
      perPage: this.pageSize,
    });
  }
  getBlogImg(blogItem: any) {
    let imgUrl = "";
    if (blogItem.id) {
      if (blogItem.labels && blogItem.labels.length) {
        // 取最后一个有图片的标签
        blogItem.labels.forEach((item: any) => {
          if (item.name.indexOf(":img") === 0) {
            imgUrl = this.$store.state.qiniuDomainName + item.name.substring(5);
          }
        });
      }
      if (!imgUrl && blogItem.milestone) {
        // 取最后一个有图片的分类
        imgUrl = blogItem.milestone.description;
      }
      // 如果标签中的图片
      // 分类中的图片
      // 默认图片
      if (!imgUrl) {
        imgUrl = this.$store.state.defaultImg;
      }
    }
    return imgUrl;
  }
  getCharCount(text: string) {
    if (text) {
      let reg = /[\u4e00-\u9fa5]/g;
      let reg2 = /[a-zA-Z]/g;
      let count1 = text.match(reg) ? text.match(reg)!.length : 0;
      // let count2 = text.match(reg2)?text.match(reg2)!.length:0
      // return (count1+count2/2).toFixed(0);
      return count1.toFixed(0);
    } else {
      return "";
    }
  }
  calcReadTime(text: string) {
    if (text) {
      let reg = /[\u4e00-\u9fa5]/g;
      let reg2 = /[a-zA-Z]/g;
      let count1 = text.match(reg) ? text.match(reg)!.length : 0;
      // let count2 = text.match(reg2)?text.match(reg2)!.length:0
      // return ((count1+count2) / 500).toFixed(0);
      return Math.ceil(count1 / 500);
    } else {
      return 0;
    }
  }
  changePage(pageNum: number) {
    console.log("changePage_____>");
    this.pageNum = pageNum;
    this.getCommentList();
  }
  async sendComment() {
    let markDownCommentBody = <string>(
      await this.$githubApi.getMdContent({ text: this.commentBody })
    );
    await this.$githubApi.createComment({
      comment: markDownCommentBody,
      issueNumber: this.blogContent.number,
    });
    this.commentBody = "";
    this.initData({ toLastPage: true });
  }
  async changePreviewMode(previewMode: boolean) {
    if (previewMode) {
      this.previewContent = <string>(
        await this.$githubApi.getMdContent({ text: this.commentBody })
      );
    } else {
      this.previewContent = "";
    }
    this.previewMode = previewMode;
  }
}
